* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-weight: normal;
  font-style: normal;
  position: relative;
  overflow-x: hidden;
  color: #242427;
  background-color: #fff;
  font-family: "Cairo", sans-serif;
  line-height: 160%;
}

body.overlay {
  overflow: hidden;
  transition: all 0.2s ease-in-out;
}

body.overlay:after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(#2e2e3e, 0.8);
  z-index: 1000;
  margin: 0;
  overflow: hidden;
}

@media only screen and (max-width: 500px) {
  .g-recaptcha {
    transform: scale(0.77);
    transform-origin: 0 0;
  }
}

a {
  text-decoration: none;
  color: #242427;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  line-height: 120%;
}

.app_btn {
  border-radius: 7px;
  border: 0;
  transition: all 0.2s ease-in-out;
  font-family: "Cairo", sans-serif;
  cursor: pointer;
  padding: 10px 20px;
  font-weight: 700;
  font-size: 15px;
  gap: 8px;
}

.app_btn.primary_bg {
  background: $primaryColor;
  color: #fff;
  &:hover {
    background: #1e21e5;
  }

  &:active {
    background: #494cff;
  }
}

.app_btn.search-btn {
  &:active {
    background: #dfeaee;
  }
  &:hover {
    background: #f5f8fa;
  }
}

.secondary_bg {
  background: #dfe0ff;
  color: $primaryColor;
  &:hover {
    background: #d3d4fb;
  }
  &:active {
    background: #edeeff;
  }
}

.gray-btn {
  background: #f0f5f7;
  transition: 0.2s;
  &:hover {
    background: #f5f8fa;
  }
  &:active {
    background: #dfeaee;
  }
}

.hidden {
  display: none !important;
}

.text-primary {
  color: $primaryColor !important;
}

.text-black {
  color: #242427 !important;
}

.main-btn {
  background: $primaryColor;
  color: #fff;
  border-radius: 7px;
  border: 0;
  transition: all 0.2s ease-in-out;
  font-family: "Cairo", sans-serif;
  cursor: pointer;
  font-size: 15px;

  &:hover {
    background: #1e21e5;
  }

  &:active {
    background: #494cff;
  }
}

p,
li {
  color: #474c4d;
  line-height: 170%;
}

ul {
  list-style: none;
  padding: 0;
}

@media #{$mobile} {
  .container {
    padding: 0 20px !important;
    position: relative;
  }
}

.w-100 {
  width: 100%;
}

.main-color {
  color: $primaryColor;
}

.hero-section {
  padding: 150px 0 100px;

  @media #{$maxTab} {
    padding: 60px 0;
  }

  @media (min-width: 1200px) and (min-height: 601px) {
    height: calc(100vh - 32px);
    display: flex;
    align-items: center;
  }

  @media (min-height: 1000px) {
    height: auto;
    padding: 135px 0;
  }

  @media (max-height: 600px) {
    height: auto;
    padding: 80px 0;
  }
}

.flex-shrink-0 {
  flex-shrink: 0;
}
.flex-grow-1 {
  flex-grow: 1;
}

button:focus,
input:focus {
  outline: none;
  box-shadow: none;
  border: 0;
}

.iti__selected-flag {
  background: #f0f5f7;
  border-radius: 105px;
  padding: 8px;
  @media screen and (min-width: 992px) {
    padding: 8px 12px;
  }
}

.iti--allow-dropdown .iti__flag-container:active .iti__selected-flag {
  background: #dfeaee;
}

.iti--allow-dropdown .iti__flag-container:hover .iti__selected-flag {
  background: #f5f8fa;
}

.iti--allow-dropdown input,
.iti--allow-dropdown input[type="text"],
