.search {
  .form-group {
    background: #ffffff;
    box-shadow: 0px 12px 50px rgba(76, 76, 108, 0.15);
    border-radius: 79px;
    padding: 8px;
    transition: 0.2s;
    border: 1px solid transparent;

    @media (min-width: 992px) {
      &:hover {
        box-shadow: 0px 4px 12px rgba(76, 76, 108, 0.15);
      }
    }

    @media #{$mobile} {
      .iti--allow-dropdown .iti__flag-container,
      .iti--separate-dial-code .iti__flag-container {
        top: 6px;
        bottom: 6px;
        left: 6px;
      }
    }

    &.focus {
      border: 1px solid #dfe6eb;
      box-shadow: 0px 6px 30px rgba(76, 76, 108, 0.11);
    }

    @media #{$mobile} {
      background: none;
      box-shadow: none;
      flex-wrap: wrap;
      padding: 0;
    }

    #email-search {
      direction: ltr;
    }
  }

  .form-control {
    display: block;
    width: 100%;
    padding: 10px 19px;
    border: 0;
    font-size: 18px;
    line-height: 32px;
    background: none;
    font-weight: 600;
    height: 100%;

    @media #{$mobile} {
      padding: 12px 19px;
      background: #fff;
      border-radius: 30px;
      position: relative;
      font-size: 15px;
      &#email-search {
        padding: 7px 19px;
        flex: 1;
      }
    }
  }

  .form-control:focus {
    color: #242427;
    outline: 0;
  }

  .search-btn {
    border-radius: 74px;
    padding: 14px 22px;
    font-weight: 700;
    font-size: 15px;
    line-height: 28px;
    border: 0;
    cursor: pointer;
    text-align: center;
    align-self: stretch;

    @media #{$mobile} {
      width: 100%;
      margin-top: 8px;
      padding: 8px 17px;
    }
  }
