// password strength meter
.password-strength-group {
  margin-top: -8px;
  padding: 0 24px;
  display: none;
  .password-strength-meter {
    width: 100%;
    transition: height 0.3s;
    display: flex;
    justify-content: stretch;
    border-radius: 70px;
    overflow: hidden;
    //offseting meter-block gaps
    .meter-block {
      height: 4px;
      background: #ccc;
      margin-right: 6px;
      flex-grow: 1;
      &:last-child {
        margin: 0;
      }
    }
  }

  .password-strength-message {
    font-size: 20px;
    height: 2em;
    text-align: left;
    transition: all 0.5s;
    margin-top: 3px;
    position: relative;
    .message-item {
      color: #777c80;
      font-weight: 600;
      font-size: 13px;
      position: absolute;
      left: 0;
      opacity: 0;
      transition: opacity 0.2s;
      display: flex;
      align-items: center;
      gap: 5px;
      span {
        font-weight: bold;
      }
    }
  }

  //define activated state:

  @for $i from 1 through 4 {
    &[data-strength="#{$i}"] {
      .meter-block:nth-child(-n + #{$i}) {
        //Switch cases color
        @if ($i == 1) {
          background: #fb5b29; // red
        } @else if($i == 2) {
          background: #ffb310; // yellow
        } @else if($i == 3) {
          background: #9ea60a; // yellowgreen
        } @else if($i == 4) {
          background: #279b37; // green
        }
      }

      .message-item:nth-child(#{$i}) {
        opacity: 1;
        span {
          @if ($i == 1) {
            color: #fb5b29; // red
          } @else if($i == 2) {
            color: #ffb310; // yellow
          } @else if($i == 3) {
            color: #9ea60a; // yellowgreen
          } @else if($i == 4) {
            color: #279b37; // green
          }
        }
      }
    }
  }
}

body.rtl {
  .password-strength-group .password-strength-message .message-item {
    left: auto;
    right: 0;
  }
}

