        width: 53px;
      }
    }
  }
}

.copied {
  width: 260px;
  opacity: 0;
  position: fixed;
  z-index: 9999;
  bottom: 20px;
  font-weight: bold;
  left: 0;
  right: 0;
  margin: auto;
  color: #242427;
  padding: 15px 30px;
  background-color: #fff;
  border-radius: 10px;
  transition: 0.3s opacity;
  box-shadow: 0px 33px 90px 0px rgba(76, 76, 108, 0.3);
}
