.wrapper {
  background-color: #ecfbff;

  @media (min-width: 992px) {
    background: #ecfbff url(../imgs/homepage/homepage-hero-bg.png) no-repeat
      center/cover;
    display: flex;
    flex-direction: column;
    height: 100vh;
  }

  @media (min-width: 1700px) {
    height: auto;
  }
  @media (max-height: 750px) {
    height: auto;
  }

  @media (min-width: 2000px) {
    .homepage-hero-image {
      height: auto;
    }
  }
}

header {
  @media screen and (min-width: 768px) {
    height: 64px;
  }

  @media #{$mobile} {
    height: 52px;
  }
}

.navbar.sticky {
  position: fixed;
  background-color: rgba(255, 255, 255, 0.98);
  box-shadow: 0px 2px 22px rgba(76, 76, 108, 0.12);
  top: 0;
}

.navbar-nav {
  img.flag {
    width: 20px;
    height: 20px;
    margin-right: 6px;
  }

  .navbar-item {
    @media (min-width: 768px) {
      width: 48px;
      height: 48px;
      position: relative;
      > a,
      span.link {
        width: 100%;
        height: 100%;
        justify-content: center;
        border-radius: 7px;
        &:hover {
          background: #f5f8fa;
        }
      }

      &.active {
        > a {
          background: #f5f8fa;
        }
      }
    }
  }

  @media #{$mobile} {
    .link {
      gap: 16px;
      padding: 7px 20px;
      position: relative;
      width: 100%;

      .link-text {
        font-size: 15px;
        font-weight: 600;
      }
    }
  }

  .link {
    .icon {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      display: flex;
      svg {
        margin: auto;
      }
    }
  }

  .badge-new {
    display: flex;
    padding: 1px 4px;
    font-size: 14px;
    font-weight: 600;
    line-height: 14px;
    border-radius: 4px;
    background: #c5e3eb;
    position: absolute;
    left: 7px;
    bottom: -4px;
    color: #242427;
    text-transform: capitalize;

    @media #{$mobile} {
      position: static;
    }
  }

  #user-dropdown {
    border-radius: 7px;
    justify-content: center;

    .avatar-text {
      background: #242427;
      justify-content: center;
      font-weight: 700;
      font-size: 16px;
      color: #fff;
    }
    .avatar-img,
    .avatar-text {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      overflow: hidden;
    }

    .avatar-img img {
      width: 100%;
      object-fit: cover;
    }

    #userDropdown {
      @media #{$mobile} {
        gap: 16px;
        padding: 13px 20px;
        min-height: 73px;
      }
      svg {
        transition: 0.3s;
        margin-left: auto;
      }
    }

    #userDropdown.active {
      svg {
        transform: rotate(180deg);
      }
    }

    .profile-btn {
      padding: 0;
      border: 0;
      background: none;
      width: 100%;
      @media #{$mobile} {
        width: auto;
      }
    }
  }

  .bg-sa {
    background: url("../imgs/langs_flags.png") -10px -10px;
  }

  .bg-us {
    background: url("../imgs/langs_flags.png") -50px -10px;
  }
}

.logo-container {
  display: flex;
  align-items: center;
  @media #{$maxTab} {
    .logo-icon {
      width: 32px;
      height: 32px;
    }
  }

  .main-logo {
    &.hide {
      display: none !important;
    }
  }

  .back-btn {
    display: none;
    transition: 0.2s;

    &.show {
      cursor: pointer;
      display: flex;
      width: 32px;
      height: 32px;
      background-color: #f5f9fb;
      border-radius: 50%;
      svg {
        margin: auto;
      }
    }

    &:hover {
      background-color: #f0f5f7;
    }
  }
}

.navbar {
  padding: 8px 0;
  width: 100%;
  z-index: 1005;

  &.bg-white {
    background: #fff;
  }

  > .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    position: static;
  }

  @media #{$mobile} {
    position: relative;

    padding: 9px 0;
  }
  @media #{$mobile} {
    .upgrade-btn {
      font-size: 13px;
      padding: 4px 8px;
      gap: 8px;
      svg {
        width: 15px;
      }
    }
  }

  .main-logo,
  .main-logo:focus,
  .main-logo:link {
    font-weight: 700;
    font-size: 20px;
    line-height: 37px;
  }

  .upgrade-btn {
    color: $primaryColor;
    &:hover {
      background: #f5f8fa;
    }
  }

  @media #{$mobile} {
    .search-btn {
      padding: 7px;
    }
  }

  .mobile-menu {
    gap: 8px;
  }

  .contact-link {
    font-weight: 600;
    font-size: 15px;
    line-height: 28px;
    display: block;

    @media #{$mobile} {
      padding: 10px 20px;
    }

    &:hover {
      color: $primaryColor;
    }
  }

  .navbar-collapse {
    @media #{$mobile} {
      order: 3;
      position: absolute;
      transform: translateX(-100%);
      transition: 0.2s ease-out;
      top: 50px;
      width: 100%;
      left: 0;
      background: #fff;
      box-shadow: inset 0px 4px 30px rgb(76 76 108 / 12%);
      visibility: hidden;

      &.active {
        transform: translateX(0);
        visibility: visible;
      }

      .navbar-nav {
        flex-direction: column-reverse;
        & + .lang-dropdown {
          border-top: 1px solid #eeeef5;
        }
        > li {
          border-top: 1px solid #eeeef5;
        }
      }

      .nav-container {
        padding: 0 !important;
      }
    }
    .navbar-nav {
      .upgrade-btn {
        gap: 8px;
      }
    }

    .navbar-item {
      position: relative;
      .notifications-number {
        position: absolute;
        right: 4.5px;
        top: 0;
        border-radius: 34px;
        padding: 0px 5px;
        background: #d80027;
        color: #fff;
        text-align: center;
        font-size: 13px;
        font-weight: 700;
        line-height: 18px;
        min-width: 20px;
        min-height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        @media #{$mobile} {
          right: 24px;
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
  }

  // Menu toggle button
  .hamburger .line {
    width: 22px;
    height: 2px;
    background-color: #242427;
    display: block;
    margin: 6px auto;
    transition: all 0.2s ease-in-out;
  }

  .hamburger:hover {
    cursor: pointer;
  }

  #hamburger-11 {
    display: inline-block;
    padding: 0 5px;
    transition: all 0.2s ease-in-out;
  }

  #hamburger-11.is-active {
    animation: smallbig 0.3s forwards;
  }

  @keyframes smallbig {
    0%,
    100% {
      transform: scale(1);
    }

    50% {
      transform: scale(0);
    }
  }

  #hamburger-11.is-active .line:nth-child(2) {
    opacity: 0;
  }

  #hamburger-11.is-active .line:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
  }

  #hamburger-11.is-active .line:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
  }

  .hamburger {
    position: relative;
    &.is-active {
      .notifications-number {
        display: none;
      }
    }
    .notifications-number {
      position: absolute;
      right: -4.5px;
      top: -4px;
      border-radius: 34px;
      padding: 0px 3px;
      background: #d80027;
      color: #fff;
      text-align: center;
      font-size: 12px;
      font-weight: 700;
      line-height: 16px;
      min-width: 16px;
      min-height: 16px;
    }
  }
}

.search-header .navbar .navbar-collapse .navbar-nav {
  @media #{$mobile} {
    flex-direction: column-reverse;
  }
}

.search-header.search-page {
  .search-item,
  .search-btn {
    display: none !important;
  }
}

.language-dropdown {
  background: transparent;
  border-radius: 7px;
  padding: 9px 11px;
  border: 0;
  font-weight: 600;
  font-size: 15px;
  line-height: 28px;
  transition: 0.2s;
  font-family: "Cairo", sans-serif;
  width: 100%;
  cursor: pointer;
  .arrow {
    transition: 0.2s;
  }

  &.active {
    .arrow {
      transform: rotate(180deg);
    }
  }

  @media #{$mobile} {
    border-radius: 0;
  }

  @media #{$mobile} {
    padding: 15px 20px;
    background: none;
  }

  &.footer-lang-dropdown {
    background: #f5f9fb;
    border-radius: 7px;
    transition: 0.2s;

    &:active {
      background: #dfeaee;
    }

    &:hover {
      background: #f5f8fa;
    }

    @media #{$mobile} {
      padding: 6px 10px;
    }
  }
}

.dropdown-menu {
  background: #ffffff;
  box-shadow: 0px 12px 22px rgba(76, 76, 108, 0.22);
  border-radius: 7px;
  border: 0;
  right: 0;
  padding: 4px;
  position: absolute;
  opacity: 0;
  visibility: hidden;
  z-index: 2;

  @media (min-width: 768px) {
    transition: 0.2s;
    &#notifications-menu,
    &#user-dropdown-menu {
      top: 45px;
      padding: 0;
      overflow: hidden;
    }
  }

  &#user-dropdown-menu {
    @media (min-width: 768px) {
      min-width: 265px;
      padding: 8px;
      max-width: 280px;
    }

    .dropdown-menu-box {
      background: #f5f9fb;
      @media (min-width: 768px) {
        padding: 16px;
        border-radius: 7px;
      }
      .dropdown-item {
        &:hover {
          background-color: #fff;
        }
      }
    }

    .search-history-link {
      padding: 6px 24px;
      transition: 0.2s;
      margin-top: 8px;
      border-radius: 3px;
      &:hover {
        background: #f5f9fb;
      }
      .badge-new {
        position: static;
      }
    }

    .dropdown-item.active:after {
      display: none;
    }
  }

  &#dropdown-menu2 {
    bottom: 100%;
  }

  &.mb-screen {
    @media #{$mobile} {
      background: #f5f9fb;
      box-shadow: none;
      border-top: 1px solid #eaeef0;
      border-radius: 0;
      padding: 0;
    }
  }

  &#notifications-menu {
    @media #{$mobile} {
      top: -74px;
      border-radius: 0;
      padding: 0;
      left: 0;
    }
    .empty-notifications {
      display: none;
      width: 400px;
      padding: 40px 60px;
      @media #{$mobile} {
        padding: 52px 20px;
        width: 100%;
      }
      .bell-icon {
        display: flex;
        width: 88px;
        height: 88px;
        border-radius: 50%;
        background-color: #f5f8fa;
        margin: auto;
        @media #{$mobile} {
          width: 64px;
          height: 64px;
          svg {
            width: 24px;
            height: 24px;
          }
        }
        svg {
          margin: auto;
        }
      }
      h3 {
        color: #242427;
        font-size: 18px;
        font-weight: 600;
        line-height: 28px; /* 155.556% */
        margin: 24px 0 12px;
        text-align: center;
        @media #{$mobile} {
          margin: 20px 0 8px;
          font-size: 15px;
        }
      }
      p {
        color: #474c4d;
        text-align: center;
        font-size: 15px;
        line-height: 25px;
        @media #{$mobile} {
          font-size: 14px;
        }
        a {
          color: $primaryColor;
          text-decoration: underline;
          transition: 0.2s;
          &:hover {
            font-weight: 600;
          }
        }
      }
    }

    .notifications {
      width: 460px;
      padding: 0;
      @media #{$mobile} {
        width: 100%;
      }
      .top {
        padding: 20px 32px;
        h3 {
          color: #242427;
          font-size: 18px;
          font-weight: 600;
          line-height: 28px; /* 155.556% */
          @media #{$mobile} {
            font-size: 16px;
          }
        }
        button {
          color: #474c4d;
          font-size: 14px;
          font-weight: 600;
          border: 0;
          background: none;
          &:hover {
            color: #cd1126;
          }
        }
      }

      .notifications-list {
        min-height: 200px;
        max-height: 465px;
        overflow: auto;
        @media #{$tablet} {
          height: 400px;
        }
        @media #{$mobile} {
          height: 300px;
        }
        li {
          padding: 12px 32px;
          text-align: left;
          position: relative;
          background: #f0f5f7;
          border-bottom: 1px solid #fff;
          &:last-child {
            border-bottom: 1px solid transparent;
          }
          @media #{$mobile} {
            padding: 8px 20px;
          }

          @media (min-width: 768px) {
            &:hover {
              background: #f0f5f7;
              .delete {
                display: flex;
              }
            }
          }
          &.read {
            background: white;
            h4 {
              font-weight: 600;
            }
          }
        }
        .icon {
          width: 40px;
          height: 40px;
          border-radius: 20px;
          box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.08);
          flex-shrink: 0;
          display: flex;
          svg {
            margin: auto;
          }
          &.congrats {
            background: #ffdec8;
          }
          &.info {
            background: #c7d7ff;
          }
          &.warn {
            background: #e9bec6;
          }
          &.success {
            background: #a2f4ad;
          }
          &.message {
            background: #dcc7ff;
          }
        }

        .info {
          max-width: 280px;
          h4 {
            color: #242427;
            font-size: 15px;
            font-weight: 700;
            line-height: 20px; /* 133.333% */
            margin-bottom: 4px;
          }
          p {
            font-size: 14px;
            line-height: 25px; /* 178.571% */
          }
        }

        .delete {
          margin-left: auto;
          width: 32px;
          height: 32px;
          flex-shrink: 0;
          background-color: #fff;
          border-radius: 50%;
          cursor: pointer;
          display: flex;
          &:hover {
            svg {
              path {
                stroke: #cd1126;
              }
            }
          }
          @media (min-width: 768px) {
            display: none;
          }
          svg {
            margin: auto;
            path {
              transition: 0.2s;
            }
          }
        }
      }

      .view-notifications {
        color: #474c4d;
        font-size: 14px;
        font-weight: 600;
        text-decoration-line: underline;
        display: block;
        padding: 12px 0px;
        text-align: center;
        border-radius: 0px 0px 7px 7px;
        margin-top: 8px;
      }
    }
  }

  &.show {
    visibility: visible;
    opacity: 1;

    &.mb-screen {
      @media #{$mobile} {
        position: relative;
        padding: 8px 0;
        left: 0;
      }
    }
  }

  .dropdown-item {
    cursor: pointer;
    border-radius: 3px;
    font-weight: 600;
    font-size: 15px;
    line-height: 28px;
    color: #242427;
    display: flex;
    align-items: center;
    min-width: 100px;
    padding: 6px 8px;
    gap: 12px;

    &.logout {
      color: #cd1126;
    }

    @media #{$mobile} {
      gap: 8px;
      &.nav-dropdown-item {
        padding: 8px 40px;
      }
    }

    &.active {
      position: relative;

      &:after {
        content: "";
        display: block;
        position: absolute;
        width: 5px;
        height: 8px;
        border-right: 2px solid #2c2ffe;
        border-bottom: 2px solid #2c2ffe;
        right: 11px;
        transform: rotate(45deg);
        top: 35%;

        @media #{$mobile} {
          right: 10px;
        }
      }
    }

    &:active {
      background: #f5f9fb;
    }

    &:hover {
      background: #f5f9fb;
    }
  }
}

#user-dropdown {
  .user-info {
    @media (min-width: 768px) {
      border-bottom: 1px solid #eeeef5;
      padding: 6px;
      margin-bottom: 8px;
    }

    h3 {
      font-weight: 600;
      font-size: 15px;
      line-height: 20px;
      color: #242427;
      margin-bottom: 3px;
    }

    div {
      display: flex;
      align-items: center;
      gap: 8px;
      p {
        font-weight: 600;
        font-size: 14px;
        line-height: 24px;
        color: #767e80;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        direction: ltr;
      }
      .label {
        font-weight: 700;
        font-size: 10px;
        line-height: 19px;
        text-transform: uppercase;
        color: #2c2ffe;
        background: #dfe0ff;
        border-radius: 47px;
        padding: 0 6px;
        margin: 0 4px;
      }
    }
  }
}

body.rtl {
  .dropdown-menu#dropdown-menu2 {
    bottom: 100%;
    left: 0;
    right: auto;
  }

  .dropdown-menu .dropdown-item.active:after {
    left: 11px;
    right: auto;

    @media #{$mobile} {
      left: 20px;
    }
  }

  .navbar-nav img.flag {
    margin-right: 0;
    margin-left: 6px;
  }

  .navbar-nav #user-dropdown .avatar-text {
    margin-right: 0;
    margin-left: 10px;
  }

  .language-dropdown .lang .ml-2,
  .dropdown-item .ml-2 {
    margin-left: 0 !important;
    margin-right: 0.5rem !important;
  }
  .dropdown-menu#user-dropdown-menu {
    right: auto;
    left: 0;
  }

  .dropdown-menu#notifications-menu .notifications .notifications-list li {
    text-align: right;
  }

  @media #{$mobile} {
    .navbar .navbar-collapse {
      transform: translateX(100%);
    }
    .navbar .navbar-collapse.active {
      transform: translateX(0);
    }
  }

  .navbar .navbar-collapse .navbar-item .notifications-number {
    left: 4.5px;
    right: auto;
    @media #{$mobile} {
      left: 24px;
      right: auto;
    }
  }

  .dropdown-menu#notifications-menu {
    @media #{$mobile} {
      right: 0;
    }
  }

  .navbar-nav #user-dropdown #userDropdown svg {
    margin-right: auto;
    margin-left: unset;
  }

  .dropdown-menu {
    right: auto;
    left: 0;
  }
}
