@import "sass/commonSections/iti";
@import "sass/global_styles/bootstrap-classes";
@import "sass/global_styles/global";
@import "sass/global_styles/header";
@import "sass/global_styles/modal";
@import "sass/global_styles/footer";

.my-inbox {
  background: #f8fbfd;
  padding: 60px 0 80px;
  @media #{$mobile} {
    padding: 20px 0 40px;
  }
  .left-col {
    border-radius: 25px;
    background: #fff;
    box-shadow: 0px 33px 90px 0px rgba(76, 76, 108, 0.1);
    padding: 25px 40px 40px;
    @media #{$mobile} {
      padding: 20px 20px 28px;
    }
    .hello-title {
      @media #{$mobile} {
        font-size: 18px;
      }
    }
    #tabs-nav {
      display: flex;
      gap: 32px;
      margin: 38px 0 32px;
      border-bottom: 1px solid #eeeef5;
      @media #{$mobile} {
        gap: 0;
        margin: 16px 0;
      }
      a {
        color: #242427;
        font-size: 15px;
        font-weight: 600;
        line-height: 25px;
        position: relative;
        padding-bottom: 8px;
        opacity: 0.6;
      }
      .badge {
        display: none;
      }

      li {
        display: flex;
        gap: 6px;
        align-items: flex-start;
        position: relative;
        @media #{$mobile} {
          width: 50%;
          justify-content: center;
        }
        &:after {
          content: "";
          width: 100%;
          height: 3px;
          background-color: #2c2ffe;
          position: absolute;
          display: none;
          bottom: -2px;
          border-radius: 100px;
        }
        &.active {
          a {
            opacity: 1;
          }

          &:after {
            display: block;
          }

          .badge {
            border-radius: 60px;
            background: #2c2ffe;
            color: #fff;
            display: inline-flex;
            width: 24px;
            height: 24px;
            align-items: center;
            justify-content: center;
            position: absolute;
            right: -30px;
            @media #{$mobile} {
              position: static;
            }
          }
        }

        &:hover {
          a {
            opacity: 1;
          }
        }
      }
    }

    #tab2 {
      display: none;
    }

    .toggle-receive-messages {
      border-radius: 11px;
      border: 1px solid #d8e2e5;
      background: #fff;
      padding: 12px 20px;
      gap: 30px;
      h4 {
        color: #242427;
        font-family: Cairo;
        font-size: 15px;
        font-style: normal;
        font-weight: 600;
        line-height: 25px;
      }
      p {
        color: #242427;
        font-family: Cairo;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: 25px;
        opacity: 0.6;
      }
      .toggle-btn {
        input[type="checkbox"] {
          height: 0;
          width: 0;
          visibility: hidden;
        }

        label {
          cursor: pointer;
          text-indent: -9999px;
          width: 40px;
          height: 24px;
          background: grey;
          display: block;
          border-radius: 100px;
          position: relative;
        }

        label:after {
          content: "";
          position: absolute;
          top: 50%;
          left: 3px;
          width: 18px;
          height: 18px;
          background: #fff;
          border-radius: 90px;
          transition: 0.3s;
          transform: translateY(-50%);
          filter: drop-shadow(0px 2px 3px rgba(76, 76, 108, 0.2));
        }

        input:checked + label {
          background: rgba(21, 207, 116, 1);
        }

        input:checked + label:after {
          left: calc(100% - 3px);
          transform: translate(-100%, -50%);
        }

        label:active:after {
          width: 22px;
        }
      }
    }

    .empty-inbox-received {
      padding: 60px 0 0;
      text-align: center;
      .text {
        h3 {
          font-size: 28px;
          margin: 40px 0 12px;
          @media #{$mobile} {
            margin: 20px 0 6px;
            font-size: 17px;
          }
        }
        p {
          font-size: 20px;
          margin: 0 auto 40px;
          max-width: 515px;
          @media #{$mobile} {
            margin: 0 auto 20px;
            font-size: 15px;
          }
        }
      }
    }

    .message {
      padding: 24px;
      border-radius: 11px;
      border: 1px solid #d8e2e5;
      background: #fff;
      @media #{$mobile} {
        padding: 20px 16px;
        .gap-16 {
          gap: 12px;
        }
      }
      .icon {
        width: 52px;
        height: 52px;
        border-radius: 50%;
        flex-shrink: 0;
        background-color: #f0f5f7;
        display: flex;
        @media #{$mobile} {
          width: 40px;
          height: 40px;
          svg {
            width: 20px;
            height: 20px;
          }
        }
        svg {
          margin: auto;
        }
      }

      .info {
        .user {
          color: #242427;
          font-size: 18px;
          font-weight: 600;
          line-height: 25px;
          word-break: break-all;
          @media #{$mobile} {
            font-size: 15px;
          }
        }
        .date {
          @media #{$mobile} {
            font-size: 14px;
          }
        }
      }

      .more-btn {
        background: none;
        width: 32px;
        height: 32px;
        border-radius: 1020px;
        display: flex;
        border: 0;
        transition: 0.2s;
        &:hover,
        &.active {
          background: #f0f5f7;
        }
        svg {
          margin: auto;
        }
      }

      .delete {
        position: absolute;
        border-radius: 7px;
        background: #fff;
        box-shadow: 0px 12px 22px 0px rgba(76, 76, 108, 0.22);
        right: 0;
        width: 183px;
        display: none;
        .delete-btn {
          padding: 10px 32px 10px 12px;
          color: #242427;
          font-size: 15px;
          font-weight: 600;
          background: none;
          border: 0;
          svg {
            flex-shrink: 0;
          }
        }
      }

      .desc {
        font-size: 18px;
        text-align: start;
        unicode-bidi: plaintext;
        @media #{$mobile} {
          font-size: 15px;
        }
      }

      .message-status {
        p {
          @media #{$mobile} {
            font-size: 14px;
            svg {
              width: 20px;
              height: 20px;
            }
          }
        }
      }

      .social-share {
        display: flex;
        justify-content: flex-end;
      }

      .share-btn {
        justify-content: flex-end;
        position: relative;
        button {
          color: #2c2ffe;
          font-size: 14px;
          font-weight: 700;
          line-height: 24px;
          padding: 8px 16px;
          transition: 0.2s;
          border-radius: 10px;
          border: 1px solid transparent;
          background: none;
          &.active,
          &:hover {
            border: 1px solid #2c2ffe;
          }
        }

        .social-box {
          background: #fff;
          border: 1px solid #eee;
          padding: 10px;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-wrap: wrap;
          > div {
            flex-wrap: wrap;
            min-width: 160px;
            justify-content: center;
          }
        }

        .social-box:after {
          display: block;
          position: absolute;
          content: "";
          width: 0;
          height: 0;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
          border-top: 10px solid #eee;
          left: 50%;
          transform: translateX(-50%);
          bottom: -10px;
        }

        .social-box:before {
          display: block;
          position: absolute;
          content: "";
          width: 0;
          height: 0;
          border-left: 9px solid transparent;
          border-right: 9px solid transparent;
          border-top: 9px solid #fff;
          bottom: -8px;
          z-index: 100;
          left: 50%;
          transform: translateX(-50%);
        }

        .social-box {
          position: absolute;
          bottom: 51px;
          border-radius: 10px;
          left: 50%;
          transform: translateX(-50%);
          box-shadow: 0px 5px 20px 0px rgba(76, 76, 108, 0.1);
          display: none;
          a {
            display: flex;
            padding: 5px;
            width: 28px;
            height: 28px;
            align-items: center;
            justify-self: center;
            &:hover {
              svg {
                path {
                  fill: #2c2ffe;
                }
              }
            }
            svg {
              margin: auto;
              path {
                transition: 0.2s;
              }
            }
          }
        }
      }
    }

    .empty-inbox-sent {
      .text {
        h3 {
          margin: 30px 0 12px;
          @media #{$mobile} {
            margin: 20px 0 6px;
            font-size: 17px;
          }
        }
        p {
          font-size: 20px;
          max-width: 500px;
          margin: auto;
          @media #{$mobile} {
            font-size: 15px;
          }
        }
      }
    }

    .sent-messages {
      .anonymous {
        border-radius: 11px;
        background: #f0f5f7;
        padding: 16px 20px;
        svg {
          flex-shrink: 0;
          position: relative;
          top: 8px;
        }
      }
    }

    .load-more {
      &:disabled {
        cursor: no-drop;
      }
      &.loading {
        background: #ededff;
        color: rgba(#2c2ffe, 0.4);
        // padding-left: 40px;
      }

      &.loading .spinner {
        position: relative;
        border-radius: 100%;
        border: 2px solid #7273ff;
        border-left-color: #2c2ffe;
        animation: spin 0.6s infinite linear;
        opacity: 0.5;
        width: 22px;
        height: 22px;
      }

      @keyframes spin {
        to {
          transform: rotate(359deg);
        }
      }
    }
  }
  .copy-link {
    border-radius: 400px;
    border: 1px solid #d7dee0;
    background: #f0f5f7;
    box-shadow: 0px 3px 6px 0px rgba(76, 76, 108, 0.06);
    max-width: 380px;
    margin: auto;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
    .link {
      padding: 12px 20px;
      max-width: 255px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: block;
      border: 0;
      background: none;
      flex-grow: 1;
      color: #777c80;
      font-size: 15px;
      font-weight: 600;
    }
    .copy-link-btn {
      border-radius: 0px 40px 40px 0px;
      border: 0;
      border-left: 1px solid #d7dee0;
      background: #fff;
      width: 123px;
      height: 48px;
      color: #242427;
      font-size: 15px;
      font-weight: 700;
      display: flex;
      gap: 8px;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      @media #{$mobile} {
        width: 53px;
      }
    }
  }
  .right-col {
    .send-message {
      padding: 34px 20px;
      border-radius: 25px;
      background: #fff;
      box-shadow: 0px 33px 90px 0px rgba(76, 76, 108, 0.1);
      @media #{$maxTab} {
        margin-bottom: 20px;
      }
      @media #{$mobile} {
        padding: 20px;
      }

      .title {
        font-size: 17px;
        @media #{$mobile} {
          font-size: 16px;
        }
      }

      .divider {
        margin: 32px 0;
        background-color: #d9d9d9;
        height: 1px;
        position: relative;
        p {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          color: #777c80;
          font-size: 15px;
          background-color: #fff;
          padding: 0 10px;
        }
      }

      .send-btn {
        padding: 14px;
        font-weight: 700;
      }
    }
  }
}

.received-messages-skeleton,
.sent-messages-skeleton {
  display: none;
  .text {
    background: #dee1e3;
    border-radius: 70px;
  }

  .text-xl {
    width: 140px;
    height: 16px;
    @media #{$mobile} {
      width: 120px;
      height: 12px;
    }
  }
  .text-lg {
    width: 95px;
    height: 16px;
    @media #{$mobile} {
      width: 110px;
      height: 12px;
    }
  }
  .text-md {
    width: 82px;
    height: 12px;
    @media #{$mobile} {
      width: 70px;
      height: 12px;
    }
  }

  .text-full {
    width: 100%;
    height: 16px;
    @media #{$mobile} {
      height: 12px;
    }
  }

  .text-half {
    width: 50%;
    height: 16px;
  }
  .main-info {
    width: 100%;
    .item {
      display: flex;
      gap: 24px;
      flex-direction: column;
      &:not(:last-child) {
        margin-bottom: 40px;
      }
      @media #{$mobile} {
        gap: 16px;
      }
    }
    .detail {
      flex: 1;
    }
    .sk-icon {
      border-radius: 50%;
      background: #eaeef0;
      flex-shrink: 0;
      margin: 0;
    }
    .icon-lg {
      width: 52px;
      height: 52px;
      @media #{$mobile} {
        width: 40px;
        height: 40px;
      }
    }
    .icon-md {
      width: 32px;
      height: 32px;
      @media #{$mobile} {
        width: 24px;
        height: 24px;
      }
    }

    .desc {
      margin-top: 32px;
      justify-content: end;
      .icon {
        width: 20px;
        height: 20px;
      }
      .text {
        width: 164px;
        height: 16px;
        border-radius: 70px;
        @media #{$mobile} {
          height: 12px;
        }
      }
    }
  }
  .loading {
    background-color: #eaeef0 !important;
    position: relative;
    overflow: hidden;
    border-radius: 70px;
  }

  .loading::before {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, transparent, #d7d7d7, transparent);
    transform: translateX(-100%);
    animation: loading 1s infinite;
  }

  @keyframes loading {
    100% {
      transform: translateX(100%);
    }
  }
}

.sent-messages-skeleton {
  .main-info {
    .desc {
      justify-content: start;
      .text {
        width: 90px;
      }
    }
  }
}

#sendMessage {
  .modal-head {
    padding: 32px;
    @media #{$mobile} {
      padding: 32px 20px 20px;
    }
    .modal-title {
      margin-bottom: 12px;
      @media #{$mobile} {
        margin-bottom: 8px;
        font-size: 17px;
      }
    }
    .modal-subtitle {
      font-size: 18px;
      font-weight: 600;
      line-height: 25px;
      @media #{$mobile} {
        font-weight: 400;
        font-size: 15px;
      }
    }
  }
  .close {
    position: absolute;
    top: 10px;
    right: 20px;
    &:hover {
      span {
        color: #242427;
      }
    }
  }

  .search__content {
    display: none;
    &.search__content--active {
      display: block;
    }
  }

  .message-content {
    padding: 0 40px 40px;

    @media #{$mobile} {
      padding: 0 20px 20px;
    }

    textarea {
      display: block;
      border-radius: 7px;
      border: 1px solid #d7dee0;
      background: #fff;
      box-shadow: 0px 3px 6px 0px rgba(76, 76, 108, 0.06);
      padding: 16px 24px;
      width: 100%;
      font-size: 15px;
      font-weight: 600;
      line-height: 24px;
      min-height: 190px;
      resize: none;
      &::placeholder {
        color: #777c80;
      }

      &:focus {
        border: 1px solid #bac0c2;
        outline: none;
      }
      &.show-error {
        border: 1px solid #c8102e;
      }
    }

    .message-length {
      display: flex;
      justify-content: flex-end;
      margin: 8px 0 24px;
      gap: 4px;
      @media #{$mobile} {
        margin: 8px 0 12px;
      }
      strong {
        color: #242427;
        &.show-error {
          color: #c8102e;
        }
      }
      span {
        color: #777c80;
      }
    }

    button {
      font-weight: 700;
      font-size: 15px;
      line-height: 28px;
      padding: 14px;
      display: block;
      width: 100%;
      color: #ffffff;
      &:disabled {
        opacity: 0.6;
        cursor: no-drop;
      }

      @media #{$mobile} {
        padding: 10px;
      }
    }

    .info {
      font-size: 15px;
      line-height: 20px;
      font-weight: 600;
      @media #{$mobile} {
        font-size: 14px;
      }
    }
  }

  .modal-content .close span {
    color: #859396;
    font-size: 35px;
    font-weight: normal;
    transition: 0.2s;
    line-height: 1;
  }

  @media (min-width: 992px) {
    .modal-lg,
    .modal-xl {
      max-width: 720px;
    }
  }

  .search__tab-container {
    li {
      border: 1px solid #cfdce3;
      &.search__tab--active {
        background: #dfe8ed;
      }
    }
  }

  .search__tab-container {
    display: flex;
    justify-content: center;
    margin: auto;
    width: fit-content;
    border-radius: 94px;
    .search__tab,
    li {
      padding: 10px 20px;
      border-radius: 94px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: capitalize;
      font-weight: 600;
      gap: 6px;
      position: relative;
      &:first-child {
        border-radius: 94px 0 0 94px;
      }
      &:last-child {
        border-radius: 0 94px 94px 0;
      }

      @media #{$mobile} {
        font-size: 13px;
        padding: 6px 10px;
      }
      span {
        background: #ffffff;
        border: 1px solid #cfdce3;
        border-radius: 58px;
        width: 16px;
        height: 16px;
        display: inline-block;
        position: relative;
        &::after {
          content: "";
          position: absolute;
          display: block;
          width: 3px;
          height: 7px;
          border-right: 2px solid #fff;
          border-bottom: 2px solid #fff;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) rotate(45deg);
          transform-origin: right;
        }
      }
    }
    .search__tab {
      cursor: pointer;
      &--active {
        font-weight: 700;
        span {
          background-color: $primaryColor;
          border: 1px solid $primaryColor;
        }
      }
    }
  }

  .iti {
    width: 100%;
  }

  .iti__selected-flag {
    border-radius: 7px;
    padding: 0 8px;
  }

  .iti--allow-dropdown .iti__flag-container,
  .iti--separate-dial-code .iti__flag-container {
    left: 4px;
    top: 4px;
    bottom: 4px;
    padding: 0;
  }

  input {
    background: #ffffff;
    border: 1px solid #d7dee0;
    box-shadow: 0px 3px 6px rgba(76, 76, 108, 0.06);
    border-radius: 7px;
    font-weight: 600;
    font-size: 15px;
    line-height: 24px;
    color: #242427;
    padding: 15px 24px;
    width: 100%;
    transition: 0.2s;

    @media #{$mobile} {
      padding: 11px 20px;
    }

    &:hover,
    &:focus {
      border: 1px solid #b9c3c7;
      box-shadow: 0px 3px 9px rgba(76, 76, 108, 0.1);
    }

    &.invalid {
      border-color: #c8102e;
    }
  }

  .error {
    font-size: 13px !important;
    color: #c8102e;
    font-weight: 600;
    margin-top: -10px;
    margin-bottom: 10px;
  }

  .message-sent-success {
    display: none;
    .body {
      padding: 40px;
      @media #{$mobile} {
        padding: 40px 20px 20px;
      }
    }
    .icon {
      @media #{$mobile} {
        svg {
          width: 44px;
          height: 44px;
        }
      }
    }
    h3 {
      font-size: 20px;
      font-weight: 700;
      line-height: 30px;
      margin: 24px 0 8px;
      @media #{$mobile} {
        margin: 16px 0 8px;
        font-size: 17px;
      }
    }
    p {
      font-size: 18px;
      @media #{$mobile} {
        font-size: 15px;
      }
      strong {
        color: #242427;
        font-weight: 600;
      }
    }

    .close-btn {
      padding: 14px;
    }
  }
}

.modal {
  .modal-content {
    background: #ffffff;
    box-shadow: 0px 33px 90px rgba(76, 76, 108, 0.1);
    border-radius: 25px;

    .modal-header {
      padding: 32px 40px;
      border: 0;

      @media #{$mobile} {
        padding: 20px;
      }
    }

    h3 {
      font-size: 28px;

      @media #{$mobile} {
        font-size: 20px;
      }
    }

    .close {
      @media #{$mobile} {
        padding: 10px 20px;
      }

      &:hover {
        span {
          color: #242427;
        }
      }

      span {
        color: #859396;
        font-size: 40px;
        font-weight: normal;
        transition: 0.2s;
      }
    }

    .body {
      padding: 0 40px 40px;

      @media #{$mobile} {
        padding: 0 20px 40px;
      }
    }
  }
}

body.rtl {
  #sendMessage .search__tab-container .search__tab:last-child {
    border-radius: 94px 0 0 94px;
  }

  #sendMessage .search__tab-container li:first-child {
    border-radius: 0 94px 94px 0;
  }

  .my-inbox .copy-link .copy-link-btn {
    border-radius: 40px 0 0 40px;
  }

  .my-inbox .left-col .message .delete {
    left: 0;
    right: auto;
  }
}
